.box{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap
}
.material-icons{
    font-size: 2.4em;
    cursor: pointer;
}
.card{
    width: 300px;
}
.card .card-image .card-title{
    color: black;
    font-weight: 400
}
.cart{
    margin-top:40px;
}
.collection .collection-item{
    display: flex;
}
.collection-item.avatar .title {
    font-size: 20px;
    font-weight: 500;
    color: #ee6e73;
    display: block;
    margin-bottom: 20px;
    
}
.collection .collection-item img{
    width: 150px;
    margin-right: 25px;
}
.item-desc{
    align-self:center;
}
.carousel.carousel-slider{
    height:312px;
}
@media only screen and (max-width: 756px) {
    .carousel.carousel-slider{
         height:96px;
    }
    .collection .collection-item img {
        width: 50px;
        margin-right: 25px;
    }
    ul:not(.browser-default) {
        width: 100%;
    }
    .card{
        width: 100%;
    }
  }
  
  
